body{
  font-family: 'Miracode';
  --normal-font: 400;
  --bold-font: 700;
  --bolder-font: 900;
  --bg-color: #fcfcfc;
  --primary-color: #65737e;
  --secondary-color: #c0c5ce;
  --primary-shadow: #8b8eaf;
  --secondary-shadow: #a17a69;
  --bottom-margin: 0.5rem;
  --bottom-margin-2: 1rem;
  --line-height: 1.7rem;
  --transition: 0.3s;
}

html {
  scroll-behavior: smooth;
}

/* CSS Resets */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  position: fixed;
  width: 100vw; /* Make the nav take the full width of the viewport */
  z-index: 1000; /* Ensure the nav stays on top of other content */
  top: 10vh;
  user-select: none;
  animation: fadeIn 5s;
  
}

.bouncy{
  height:100vh
}

marquee{
  height: 100vh;
  animation: fadeIn 7s
}

.homepage{
  display: inline-block;
  justify-content: center;
  pointer-events: all;
}

.homepage-container {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; /* Ensure the section takes the full viewport height */
  animation: moveToTop 1s forwards;
  animation-delay: 2s; 
  pointer-events: none;
  width: 100%;
  
}
.introduction{
  font-family: Miracode;
  font-size: 5vw;
  letter-spacing: 8px;
  border-right: 5px solid;
  color: #c0c5ce;
  border-color: #c0c5ce;
  white-space: nowrap;
  overflow: hidden;
  height: 10%;
  width: 100%;
  animation:
    typing 2s steps(14),
    cursor .4s step-end infinite alternate;
    
}

.introduction:hover{
  color: #65737e;
  border-color: #65737e;
}

.subtext-intro{
  text-align: right;
  display: block;
  width: 100%;
  letter-spacing: 5px;
  white-space: nowrap;
  border-color: white;
  overflow: hidden;
  animation: fadeIn 5s;
  text-align: right;
}

@keyframes fadeIn{
  0%{opacity: 0;}
  100%{opacity: 1;}
}

@keyframes cursor{
  50% {border-color: transparent}
}

@keyframes typing{
  from {width: 0}
}

@keyframes moveToTop{
  0%{
    transform: translateY(0);
  }
  100%{
    transform: translateY(-44vh);
  }
}

.navigation{
  display:flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;  
  height: 1vw;
}

li{
  margin-left: 10px;
  margin-right: 10px;
}

a {
  text-decoration: none;
  color: var(--primary-color);
  margin-left: 5px;
  font-size: 1.2vw;
}

a:hover {
  color: var(--secondary-color);
  text-decoration: underline;
}

