.stars{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.star{
  position:absolute;
  top: -50px; /* Adjust the starting position of the stars */
  right: 0;
  transform: rotateZ(45deg);
}

.star:nth-child(2){
  right:100px
}
.star:nth-child(2) span{
  animation-delay: 1s;
}

.star:nth-child(3){
  right:450px
}
.star:nth-child(3) span{
  animation-delay: 2s;
}

.star:nth-child(4){
  right:-150px
}
.star:nth-child(4) span{
  animation-delay: 3s;
}

.star:nth-child(5){
  right:-450px
}
.star:nth-child(5) span{
  animation-delay: 4s;
}

.shootingstar{
  width: 140px;
  height: 2px;
  display: block;
  background-image: linear-gradient(to right, #fff, transparent);
  margin:50px;
  margin-bottom:150px;
  position:relative;
  animation: shooting 2s linear infinite;
}

.shootingstar::before{
  content:"";
  position:absolute;
  bottom:-1px;
  left:-1px;
  width:5px;
  height:5px;
  background-color: #fff;
  box-shadow: 0 0 6px #fff;
  border-radius: 50px;
}

@keyframes shooting{
  0%{
    transform: translateY(-200px) rotate(-90deg);
    opacity: 1;
  }
  100%{
    transform: translateY(900px) rotate(-90deg);
    opacity: 0;
  }
}